*, body, .container-fluid {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.main-container {
  content: '';
 // background-image: url('./wald-form-images/Jabreau-2560x1440.jpg');
}

.App {
    text-align: center;
  
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 80px;
  }
  
  .App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
  }
  
  .App-title {
    font-size: 1.5em;
  }
  
  .App-intro {
    font-size: large;
  }
  
  @keyframes App-logo-spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
  }

}